<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Browse record</h1>
      <div class="chartWrap">
        <a class="iconfont cart" @click="$router.push('/cart')"> &#xe669; </a>
      </div>
    </section>
    <section class="downListWrap">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh" loosing-text='Release to refresh' loading-text='Loading...'>
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="The end"
          @load="onLoad"
        >
          <div class="commonPro">
            <ul
              class="clearfix"
              :class="{ turnLine: showStyle == 1, Prolist: showStyle == 2 }"
            >
              <li
                v-for="(item, index) in list"
                :key="index"
                @click="
                  $router.push({
                    path: 'proDetail',
                    query: { spuCode: item.spuCode }
                  })
                "
              >
                <div class="tips_cutOff" v-if="item.percentage !='0%'" >
                  {{ item.percentage }} OFF
                </div>
                <div class="tips_sale" v-if="item.sale"></div>
                <div class="imgWrap">
                  <img class="img" :src="item.spuMainImage" />
                </div>
                <div class="rightMain">
                  <div class="name">{{ item.name }}</div>
                  <div class="downWrap">
                    <div class="bottomPrice">
                      <p class="red">{{ getStr(item.priceRange) }} AED</p>
                      <p class="gray">{{ getStr(item.priceRangeLine) }} AED</p>
                      <p v-if="item.isTypeFour == 1">Free Shipping</p>
                    </div>
                    <div class="downShare">
                      <p class="others">{{ item.sumSku }} Other Items</p>
                      <div
                        :class="{ active: item.cart }"
                        @click.stop="addChart(item)"
                      >
                        <span class="cart iconfont"> &#xe669; </span>
                      </div>
                      <div
                        :class="{ active: item.share }"
                        @click.stop="shareShow(item)"
                      >
                        <span class="share iconfont"> &#xe650; </span>
                      </div>
                      <div
                        :class="{ active: item.share }"
                        @click.stop="deleteRecord(item,index)"
                      >
                        <span class="share iconfont"> &#xe609; </span>
                      </div>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </van-list>
      </van-pull-refresh>
    </section>

    <!-- 添加购物车 -->
    <add-cart ref="addCart"> </add-cart>
  </div>
</template>

<script>
import { getUserBrowseProduct, deleteUserBrowseProduct } from "@/api/user";
export default {
  name: "browseRecord",
  data() {
    return {
      list: {},
      sortVisible: false,
      searchValue: "",
      sortValue: 2,
      query: {},
      loading: false,
      finished: false,
      isLoading: true,
      sortType: [
        { value: 1, label: "Price:Low to High" },
        { value: 2, label: "Price:High to Low" },
        { value: 3, label: "Best Sale" },
        { value: 4, label: "High Rated" }
      ],
      showStyle: 1,
      show: false,
      toggle: {
        Categories: 1,
        Brand: 0
      },
      priceRange: ["", ""],
      radioValue: 1,
      radioValue2: 1
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
    this.query = Object.assign(this.query, { pageNo: 1, pageSize: 10 });
      this.$store.dispatch('getUserBrowseProduct',this.query)
        .then(res => {
          this.list = res;
          this.isLoading = false;
          if (res.length==0) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
          this.isLoading = false;
        });
    },
    onLoad() {
      console.log(1)
      if(this.isLoading){
        this.loading=false;
        return false;
      }
      this.query.pageNo++;
      this.$store.dispatch('getUserBrowseProduct',this.query)
        .then(res => {
          this.list = this.list.concat(res);
          this.loading=false;
          if (res.length==0) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
            this.loading=false;
        });
    },
    onRefresh() {
      this.init();
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    }, // 弹出规格
    addChart(data) {
      this.$refs.addCart.load(data);
    },
    deleteRecord(item,index) {
      this.$store.dispatch('deleteUserBrowseProduct',{index:index, id: item.id }).then(res => {
        this.init();
      });
    },
    shareShow(item) {
        this.$store
        .dispatch("share", {
          imageUrl: [item.spuMainImage],
          url: "proDetail?spuCode="+item.spuCode, 
          title: item.name,
        })
        .then(res => {});
    },
    getStr(obj){
      var index = obj.indexOf("-");
      if(index != -1){
        var str = obj.substring(0,index-1);
        return str;
      }else{
        return obj;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/search.scss";
// .downShare{
//   position: absolute !important;
//   width: 90px;
//   right: 20px;
//   bottom: 15px;
// }
</style>



